<template>
<div>
  <div class="header_box">
        <img src="../assets/images/goback_b.png" class="left" id="props" @click="$router.back(-1)">
        <p class="midden">{{ this.head_name }}</p>
    </div>
    <div class="h50"></div>
  </div>
</template>

<script>

export default {
    props: {
      head_name: {
        type:String,
        default:''
      }
    },
  
   data() {
        return {
            name : this.myName    // 
        }
    },
    watch: {
        myName(newVal) {
            this.name = newVal //
        }
    },
    methods: {
        changeName() {  
            this.name = 'Lily'  // 
        },
    }
}
</script>
<style>
@import '../assets/css/header_common.css'; 
</style>
  



