//  js 
 export  function getchilds(obj){
                 var childs=obj.childNodes;
                var arr=[];
                         for(var i=0; i<childs.length;i++){
          if(childs[i].nodeType==3 && /^\s+$/.test(childs[i].nodeValue)){
                       continue;
                                  }else{
                           arr.push(childs[i])
                  }
                         }
                         return arr;
                 }

     
/**
 * @description 
 * @param {Function} func 
 * @param {Number} wait ,200
 * @param {Number} type 1 2 
 */

export function throttle(func, wait=1000 ,type) {
  if(type===1){
      let previous = 0;
  }else if(type===2){
      let timeout;
  }
  return function() {
      let that= this;
      let args = arguments;
      if(type===1){
          let now = Date.now();

          if (now - previous > wait) {
              // func.apply(that, args); // 
              that[func](...args); // vue
              previous = now;
          }
      }else if(type===2){
          if (!timeout) {
              timeout = setTimeout(() => {
                  timeout = null;
                  // func.apply(that, args)
                  that[func](...args); // vue
              }, wait)
          }
      }
  }
}


